.each-anim(@tag, @startclass: 'loaded', @animationDuration: 1s, @animationDelay: 0.1s ) {
	.@{tag} {
	  opacity: 0;
	  //transform: rotateX(-90deg);
	  .rotateX(-90deg);
	  .transition(all 0.5s cubic-bezier(.36,-0.64,.34,1.76));
 	}
	body.@{startclass} & .@{tag} {
	  opacity: 1;
	  transform: none;
	}
	//animation delay
  body.@{startclass} & .@{tag} {
    .animation (50);
    .animation(@n, @i: 1) when (@i =< @n) {
        &:nth-child(@{i}) {
          .transition-delay(@i * @animationDelay);
          //z-index: @i;
          z-index: 0;
        }
        .animation(@n, (@i + 1));
    }
  }
}


// ANIMATION
// --------------------------------------------------

.animation(@name; @duration: 1s; @ease: ease-in) {
	-webkit-animation:	@name @duration @ease;
	-moz-animation:		@name @duration @ease;
	-ms-animation:		@name @duration @ease;
	-o-animation:		@name @duration @ease;
	animation:			@name @duration @ease;
}


// Fade in
// -------------------------

@-webkit-keyframes fadeIn {
	from { opacity: 0;}
	to   { opacity: 1; }
}
@-moz-keyframes fadeIn {
	from { opacity: 0;}
	to   { opacity: 1; }
}
@-o-keyframes fadeIn {
	from { opacity: 0;}
	to   { opacity: 1; }
}
@-ms-keyframes fadeIn {
	from { opacity: 0;}
	to   { opacity: 1; }
}
@keyframes fadeIn {
	from { opacity: 0;}
	to   { opacity: 1; }
}


// Zoom down
// -------------------------

@-webkit-keyframes zoom-down {
	from { .transform-origin(top);
		   .scale(0,0); }
	to   { .scale(1,1); }
}
@-moz-keyframes zoom-down {
	from { .transform-origin(top);
		   .scale(0,0); }
	to   { .scale(1,1); }
}
@-o-keyframes zoom-down {
